@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
$--hot-width: 405px * 2 +  10;
$--hot-height: 190px + 178 + 69 * 2 + 10;

%title {
  @extend %text-overflow;

  padding-left: 15px;
}

%text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@include b(dot) {
  position: absolute;
  top: 50%;
  left: 0;
  width: 5px;
  height: 5px;
  margin-top: -2.5px;
  background-color: #cc2a44;
  border-radius: 50%;
}

@include b(mien) {
  overflow: hidden;
  background-color: $--background-color-page;

  @include e(content) {
    width: $--content-width;
    margin: 0 auto 44px;
    overflow: hidden;
  }

  @include e(header) {
    height: 32px;
    margin-bottom: 20px;
    line-height: 32px;
    border-bottom: 1px solid #323232;
  }

  @include e(title) {
    position: relative;
    display: inline-block;
    vertical-align: top;

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      content: '\20';
      border-bottom: 2px solid #cc2944;
    }
  }

  @include e(more-link) {
    position: relative;
    float: right;
    margin-right: 4px;
    color: $--color-primary;

    &::after {
      margin-top: -4px;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  @include e(module) {
    margin-top: 30px;
  }
}

@include b(multiple-list) {
  background-color: $--background-color-base;

  @include e(item) {
    position: relative;
    display: block;
    height: $--hot-height / 6;
    padding-right: 74px;
    padding-left: 10px;
    overflow: hidden;
    transition: all 0.2s ease;

    &:hover {
      background-color: #2a2a2a;
    }
  }

  @include e(title-wrapper) {
    position: relative;
    margin-top: 10px;
  }

  @include e(title) {
    @extend %title;

    font-size: 13px;
    line-height: 24px;
    color: #ccc;
  }

  @include e(subtitle) {
    @extend %title;

    font-size: 12px;
    line-height: 18px;
    color: #616161;
  }

  @include e(img) {
    position: absolute;
    top: 50%;
    right: 10px;
    width: 64px;
    height: 64px;
    margin-top: -32px;
    overflow: hidden;
    font-size: 0;
  }
}

@include b(single-list) {
  overflow: hidden;
  background-color: $--background-color-base;

  @include e(item) {
    position: relative;
    display: block;
    padding: 0 20px;
    font-size: 14px;
    transition: all 0.2s ease;

    &:hover {
      background-color: #2a2a2a;

      @include e(item-inner) {
        border-bottom: 1px solid transparent;
      }
    }
  }

  @include e(skeleton) {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 20px;
    margin-left: 15px;
    overflow: hidden;
    vertical-align: middle;
    background-color: #333;

    &::before {
      position: absolute;
      top: 0;
      z-index: 99;
      display: block;
      width: 30%;
      height: 100%;
      content: "";
      background: linear-gradient(to right, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.3) 50%, rgba(255, 255, 255, 0) 100%);
      transform: skewX(-45deg);
      animation: flash 1s ease infinite;
    }

    @keyframes flash {
      from {
        left: -100%;
      }

      to {
        left: 150%;
      }
    }
  }

  @include e(item-inner) {
    position: relative;
    padding-right: 100px;
    border-bottom: 1px solid #1e1e1e;
    transition: all 0.2s ease;
  }

  @include e(title) {
    @extend %title;

    color: #ccc;
  }

  @include e(date) {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -8px;
    line-height: 16px;
    color: #5f5f5f;
  }

  &.no-events {
    pointer-events: none;
  }
}

@include b(hot) {
  float: left;
  width: $--hot-width;

  @include e(list) {
    height: $--hot-height;
  }

  @include e(item) {
    float: left;
    width: 266px;

    & + & {
      margin-left: 11px;
    }

    &:nth-child(3) {
      margin-left: 0;
    }

    @include m(max) {
      width: 405px;
      margin-bottom: 10px;

      & + & {
        margin-left: 10px;
      }
    }

    &:hover {
      @include e(info-inner) {
        max-height: 100px;
      }
    }
  }

  @include e(img-wrapper) {
    position: relative;
    display: block;
    height: 178px;
    overflow: hidden;

    @include m(max) {
      height: 190px;
    }

    img {
      vertical-align: top;
    }

    &:hover {
      @include e(mask) {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  @include e(img) {
    height: 100%;
    vertical-align: top;
  }

  @include e(mask) {
    @extend %hover-mask;
  }

  @include e(icon-play) {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    margin-top: -30px;
    margin-left: -30px;
    cursor: pointer;
    background: url(~@/views/mien/img/play.png) no-repeat center/60px;
  }

  @include e(info) {
    position: relative;
    z-index: 1;
    height: 69px;
    cursor: default;
  }

  @include e(info-inner) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    max-height: 100%;
    overflow: hidden;
    background-color: #2a2a2a;
    transition: all 0.2s ease;
  }

  @include e(title) {
    @extend %title;

    margin-top: 10px;
    font-size: 15px;
    line-height: 24px;
    color: #ccc;
  }

  @include e(subtitle) {
    @extend %title;

    font-size: 12px;
    line-height: 18px;
    color: #616161;
  }

  @include e(link-wrapper) {
    margin: 15px 0;
    font-size: 14px;
    line-height: 20px;
    color: $--color-primary;
    text-align: center;

    a:hover {
      text-decoration: underline;
    }
  }
}

@include b(latest) {
  float: right;
  width: $--content-width - $--hot-width - 10;

  @include e(list) {
    height: $--hot-height;
  }
}

@include b(news) {
  float: left;
  width: 640px;

  @include e(list) {
    height: 48px * 6;
  }

  .single-list__item {
    height: 48px;
    line-height: 48px;
  }
}

@include b(fund) {
  $--width: $--content-width - 640 -10;

  float: right;
  width: $--width;
  overflow: hidden;

  @include e(list) {
    width: $--width +20px;
    height: 48px * 6;
    overflow: auto;
  }

  @include e(list-inner) {
    width: $--width;
  }

  @include e(item) {
    & + & {
      &::after {
        position: absolute;
        top: 0;
        right: 10px;
        left: 10px;
        height: 1px;
        content: '\20';
        border-bottom: 1px solid #1e1e1e;
      }
    }

    &:hover {
      &::after {
        display: none;
      }
    }
  }
}

@include b(video) {
  float: left;
  width: 640px;

  @include e(inner) {
    position: relative;
    height: 168px;
    overflow: hidden;
    cursor: pointer;
  }

  @include e(img) {
    vertical-align: top;
  }

  @include e(flag) {
    position: absolute;
    right: 10px;
    bottom: 7px;
    width: 88px;
    height: 24px;
    font-size: 14px;
    line-height: 24px;
    color: #fff;
    text-align: center;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 4px;
  }
}

@include b(information) {
  float: right;
  width: $--content-width - 640 -10;

  @include e(list) {
    height: 168px;
  }

  .single-list__item {
    height: (168px / 3);
    line-height: (168px / 3);
  }
}

@include b(history) {
  position: relative;
  padding: 30px;
  background-color: $--background-color-base;

  @include e(axis) {
    position: absolute;
    top: 50%;
    right: 30px;
    left: 30px;
    height: 2px;
    margin-top: -1px;
    border-bottom: 2px solid #303030;

    &::after,
    &::before {
      position: absolute;
      top: -3px;
      width: 8px;
      height: 8px;
      content: '\20';
      background-color: #303030;
      border-radius: 50%;
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
    }
  }

  @include e(item) {
    position: relative;
    display: inline-block;
    width: 240px;

    &.on {
      @include e(icon-triangle) {
        background: url(./img/icon_triangle_down.png) no-repeat;
      }

      @include e(icon-dot) {
        bottom: -13px;
      }

      @include e(date) {
        bottom: -40px;
      }
    }

    &.under {
      left: 190px;

      @include e(icon-triangle) {
        background: url(./img/icon_triangle_up.png) no-repeat;
      }

      @include e(icon-dot) {
        top: -13px;
      }

      @include e(date) {
        top: -40px;
      }
    }
  }

  @include e(content) {
    padding: 10px;
    background-color: #303030;
  }

  @include e(img-wrapper) {
    position: relative;
    height: 100px;
    overflow: hidden;

    &:not(.test,[src]) {
      color: red;
    }
  }

  @include e(img) {
    width: auto;
    height: 100%;
    vertical-align: top;
  }

  @include e(title) {
    height: 36px;
    margin-top: 8px;
    overflow: hidden;
    font-size: 13px;
    line-height: 18px;
    color: #b8b8b8;

    &.link {
      cursor: pointer;
    }
  }

  @include e(icon-arrow) {
    display: inline-block;
    width: 18px;
    height: 18px;
    margin-left: 4px;
    vertical-align: top;
    background: url(./img/icon_arrow_right.png);
  }

  @include e(icon-triangle) {
    display: block;
    width: 27px;
    height: 19px;
    margin: 0 auto;

    &.on {
      background: url(./img/icon_triangle_down.png) no-repeat;
    }

    &.under {
      background: url(./img/icon_triangle_up.png) no-repeat;
    }
  }

  @include e(icon-dot) {
    position: relative;
    display: block;
    width: 26px;
    height: 26px;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 50%;

    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 8px;
      height: 8px;
      margin-top: -4px;
      margin-left: -4px;
      content: '\20';
      background-color: #c08f4d;
      border-radius: 50%;
    }

    &.on {
      bottom: -13px;
    }

    &.under {
      top: -13px;
    }
  }

  @include e(date) {
    position: absolute;
    left: 0;
    z-index: 2;
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    color: #b8b8b8;
    text-align: center;

    &.on {
      bottom: -40px;
    }

    &.under {
      top: -40px;
    }
  }

  @include e(icon-next) {
    position: absolute;
    top: auto;
    bottom: 8px;
    left: 50%;
    width: 32px;
    height: 12px;
    margin-left: -16px;
    background: url(./img/icon_next.png) no-repeat;
    animation: shaking 2s infinite;

    @keyframes shaking {
      0%,
      20%,
      50%,
      80%,
      to {
        transform: translateX(0);
      }

      40% {
        transform: translateX(20px);
      }

      60% {
        transform: translateX(15px);
      }
    }

    &::after {
      display: none;
    }

    &:global(.swiper-button-disabled) {
      animation: none;
    }
  }

  :global(.swiper-container) {
    height: 209px * 2;
  }

  :global(.swiper-slide) {
    height: 209px;
  }
}
